import type { LoaderFunction } from "@remix-run/server-runtime";
import { json } from "@remix-run/node";
import type { ActionFunction } from "@remix-run/node";
import { Form, useActionData, useLoaderData, useTransition } from "@remix-run/react";
import invariant from "tiny-invariant";

import type { Post } from "~/models/post.server";
import { getPost } from "~/models/post.server";
import { marked } from "marked";
import { useState } from "react";

type LoaderData = { post: Post; html: string };

export const loader: LoaderFunction = async ({ params }) => {
  invariant(params.slug, `params.slug is required`);
  const post = await getPost(params.slug || "");
  invariant(post, `Post not found: ${params.slug}`);
  const html = marked(post.markdown);
  return json<LoaderData>({ post, html });
};

type ActionData =
  | {
      title: null | string;
      slug: null | string;
      markdown: null | string;
    }
  | undefined;

export const action: ActionFunction = async ({ request }: { request: any }) => {
  await new Promise((res) => setTimeout(res, 1000));
  const formData = await request.formData();
  const title = formData.get("title");
  const slug = formData.get("slug");
  const markdown = formData.get("markdown");

  const errors: ActionData = {
    title: title ? null : "Title is required",
    slug: slug ? null : "Slug is required",
    markdown: markdown ? null : "Markdown is required",
  };

  const hasErrors = Object.values(errors).some((errorMessage) => errorMessage);

  if (hasErrors) {
    return json<ActionData>(errors);
  }

  invariant(typeof title === "string", "title must be a string");
  invariant(typeof slug === "string", "slug must be a string");
  invariant(typeof markdown === "string", "markdown must be a string");
};

export default function AdminPost() {

  const inputClassName = `w-full rounded border border-gray-500 px-2 py-1 text-lg`;
  const transition = useTransition();
  const isSaving = Boolean(transition.submission);
  // eslint-disable-next-line react-hooks/rules-of-hooks
  const [isEditMode, setIsEditMode] = useState(false);
  // eslint-disable-next-line react-hooks/rules-of-hooks
  const { post, html } = useLoaderData();
const errors = useActionData()

  return (
    <main className="mx-auto max-w-4xl">
      {isEditMode ? (
        <Form method="post">
          <p>
            <label htmlFor="">
              Post Title:
              {errors?.title ? (
                <em className="text-red-600"> {errors.title}</em>
              ) : null}
              <input type="text" name="title" className={inputClassName} />
            </label>
          </p>
          <p>
            <label htmlFor="">
              Post Slug:
              {errors?.slug ? (
                <em className="text-red-600"> {errors.slug}</em>
              ) : null}
              <input type="text" name="slug" className={inputClassName} />
            </label>
          </p>
          <p>
            <label htmlFor="markdown">
              Markdown:
              {errors?.markdown ? (
                <em className="text-red-600"> {errors.markdown}</em>
              ) : null}
            </label>
            <br />
            <textarea
              name="markdown"
              id="markdown"
              rows={20}
              className={`${inputClassName} font-mono`}
            ></textarea>
          </p>
          <p className="text-right">
            <button
              type="submit"
              className="rounded bg-blue-500 py-2 px-4 text-white hover:bg-blue-600 focus:bg-blue-400 disabled:bg-blue-300"
              disabled={isSaving}
              onClick={() => setIsEditMode(false)}
            >
              {isSaving ? "Saving..." : "Save Post"}
            </button>
          </p>
        </Form>
      ) : (
        <>
          {" "}
          <h1 className="my-6 text-left text-3xl">{post.title}</h1>
          <div dangerouslySetInnerHTML={{ __html: html }} />
          <div className="flex-end flex w-full">
            <button
              className="mx-4 w-40 rounded bg-green-500"
              onClick={() => setIsEditMode(true)}
            >
              Edit Post
            </button>
            <button className="mx-4  w-40 rounded bg-red-500 py-2">
              Delete Post
            </button>
          </div>
        </>
      )}
    </main>
  );
}
